.app{min-height:100vh;width:100%}header{width:100%;position:sticky;left:0;top:0;padding:.5rem 2rem;display:flex;gap:.5rem;justify-content:center}header a{padding:.25rem;background:hsla(0,0%,100%,.85)}header a:hover{color:#e24345}@media print{header{display:none}}.container{display:flex;flex-wrap:wrap;justify-content:center;width:100%;padding:2rem .5rem}@media print{.container{padding:0}}.container .inner{max-width:900px;width:100%;gap:1rem 1rem;display:grid;grid-template-columns:73% 27%;grid-template-rows:auto}@media print{.container .inner{max-width:700px}}.container main{display:flex;flex-direction:column;gap:1rem}.container aside{display:flex;flex-direction:column;gap:1rem}