.container{display:flex;flex-direction:column;gap:.65rem}.container .block h3{margin-bottom:.25rem}.container .block.primary ul{display:flex;flex-direction:column;gap:.2rem;font-size:90%}.container .block.primary li{color:#fff;border-radius:.25rem;padding:.175rem .25rem;background:linear-gradient(to right, #a31918, #f23837);border:1px solid #ab5656}.container .block.additional ul{display:flex;flex-direction:column;gap:.2rem;padding:0 .2rem}.container .block.additional li{position:relative;padding-left:.5rem}.container .block.additional li::before{position:absolute;left:-0.275rem;content:"-";color:#748fb2;margin-right:.25rem;margin-left:.1rem;font-weight:700}