.container{display:flex;gap:1.5rem}.container a{color:#cc3433}.container a:hover{text-decoration:underline}@media print{.container a{color:#354054}}.info{display:flex;flex-direction:column;gap:.5rem;justify-content:center;font-weight:500;color:#4a5b7b}.info>div{display:flex;align-items:center;gap:.5rem}.info .icon{width:20px;height:20px}.info .icon.fill path{fill:#4a5b7b}.info .icon.stroke path{stroke:#4a5b7b}.img{width:200px;height:200px;overflow:hidden;background:#eaeaea;border-radius:100%}.img img{object-fit:cover;object-position:top;width:100%;height:100%;padding:.5rem .5rem 0}